import React from 'react'
import { makeStyles } from '@material-ui/styles';
import { CircularProgress, Typography } from '@material-ui/core';

const useStyles = makeStyles({
  containerAbsolute:{
    position: 'absolute',
    width: '100%',
    height: '100%',
    top: 0,
    background: '#ffffffa0',
    zIndex: 1101,
    display: 'table',
    touchAction: 'none',
  },
  containerWide: {
    width: '100%',
  },
  centerd: {
    display: 'table-cell',
    verticalAlign: 'middle',
    textAlign: 'center',
  }
})

export default function Loader({message, absolute}) {
  const classes = useStyles()
  const container = absolute? classes.containerAbsolute: classes.containerWide
  return (
    <div className={container}> 
      <div className={classes.centerd}>
      <CircularProgress/>
      <Typography color="primary">{message}</Typography>
      </div>
    </div>
  )
}
